import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',redirect:'/index'},
    {path: '/index',component:resolve=>{require(['@/page/index/index'],resolve)}},
    {path: '/register',component:resolve=>{require(['@/page/index/register'],resolve)}},
    {path: '/sign-modify',redirect:'/modify',component:resolve=>{require(['@/page/index/modify/sign-modify'],resolve)},
      children:[
        {path: '/modify',component:resolve=>{require(['@/page/index/modify/modify'],resolve)}},
        {path: '/modifylogin',component:resolve=>{require(['@/page/index/modify/modifylogin'],resolve)}},
      ]
    },
    {path: '/sidebar',redirect:'/mycar',component:resolve=>{require(['@/page/fleet/sidebar'],resolve)},
        children:[
          {path: '/mycar',component:resolve=>{require(['@/page/fleet/mycar'],resolve)}},
          {path: '/inquiry',component:resolve=>{require(['@/page/fleet/inquiry'],resolve)}},
          {path: '/shared',component:resolve=>{require(['@/page/fleet/shared'],resolve)}},
          {path: '/vehicle',component:resolve=>{require(['@/page/fleet/vehicle'],resolve)}},
          {path: '/fleetone',component:resolve=>{require(['@/page/fleet/fleetone'],resolve)}},
          {path: '/waybill',component:resolve=>{require(['@/page/fleet/waybill'],resolve)}},
          {path: '/amendment-o',component:resolve=>{require(['@/page/fleet/amendment-o'],resolve)}},
        ]

    },
    {path: '/trajectory',redirect:'/vehiclePosition',component:resolve=>{require(['@/page/fleet/trajectory'],resolve)},
      children:[
          {path: '/mycar',component:resolve=>{require(['@/page/fleet/mycar'],resolve)}},
          {path: '/inquiry',component:resolve=>{require(['@/page/fleet/inquiry'],resolve)}},
          {path: '/shared',component:resolve=>{require(['@/page/fleet/shared'],resolve)}},
          {path: '/vehiclePosition',redirect:'/history-trajectory',component:resolve=>{require(['@/page/fleet/vehiclePosition'],resolve)},
            children:[
                {path: '/history-trajectory',component:resolve=>{require(['@/page/fleet/history-trajectory'],resolve)}},
                {path: '/history-direction',component:resolve=>{require(['@/page/fleet/history-direction'],resolve)}},
            ]
          }
        ]
    },
    {path: '/mangement',component:resolve=>{require(['@/page/mangement/mangement'],resolve)}},
    {path: '/amendment',component:resolve=>{require(['@/page/fleet/amendment'],resolve)}},
    // {path: '/vehicle',component:resolve=>{require(['@/page/fleet/vehicle'],resolve)}},
    {path: '/invoices',component:resolve=>{require(['@/page/invoices/invoices'],resolve)}},
    {path: '/billcord',component:resolve=>{require(['@/page/invoices/billcord'],resolve)}},
    {path: '/manuscript',component:resolve=>{require(['@/page/record/manuscript'],resolve)}},
    {path: '/record',component:resolve=>{require(['@/page/record/record'],resolve)}},
    {path: '/logging',component:resolve=>{require(['@/page/record/logging'],resolve)}},
    {path: '/business',component:resolve=>{require(['@/page/business/business'],resolve)}},
    {path: '/vip',component:resolve=>{require(['@/page/business/vip'],resolve)}},
    {path: '/account',component:resolve=>{require(['@/page/account/account'],resolve)}},
    {path: '/rechange',redirect:'/stem',component:resolve=>{require(['@/page/account/rechange'],resolve)},
      children:[
        {path: '/stem',redirect:'/meth',component:resolve=>{require(['@/page/account/stem'],resolve)},
          children:[
            {path: '/meth',component:resolve=>{require(['@/page/account/meth'],resolve)}},
            {path: '/account_rechange',component:resolve=>{require(['@/page/account/account_rechange'],resolve)}},
            {path: '/repayment',component:resolve=>{require(['@/page/account/repayment'],resolve)}},
          ]
        },
      ]
    }
  ]
})



